"use client";
import gsap from "gsap";
import { useGSAP } from "@gsap/react";

export default function Hero() {
  useGSAP(() => {
    gsap.from(".hero-text", {
      y: 100,
      opacity: 0,
      duration: 1.2,
      ease: "power4.out",
      stagger: 0.2
    });
  });

  return (
    <div className="flex flex-col items-center justify-center text-center py-20 bg-[#060606]">
      <span className="hero-text text-[#00df82] uppercase tracking-[0.4em] text-[10px] font-bold mb-10">
        Innovative Design
      </span>
      
      <h1 className="hero-text text-7xl md:text-[10rem] font-bold text-white tracking-tighter leading-none mb-12">
        Visual <span className="text-[#00df82] italic font-black">Impact</span>
      </h1>

      <p className="hero-text text-zinc-500 text-base md:text-lg max-w-xl leading-relaxed px-4">
        Scroll down to see our curated portfolio reveal in high <br /> resolution.
      </p>
    </div>
  );
}